import React from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";
import Drumscript from './components/drumscript'
import Profile from './components/profile'

function App() {
  return(
    <div>
      <Router>
        <Switch>
           <Route path="/drumscript" component={Drumscript} />
           <Route path="/" component={Profile} />
        </Switch>
      </Router>
    </div>  
  )
}

export default App;

